import _ from 'lodash';


function shuffleArray(array) {
  const arr = [...array]; // tạo bản sao để không làm thay đổi mảng gốc
  for (let i = arr.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1)); // số ngẫu nhiên từ 0 đến i
      [arr[i], arr[j]] = [arr[j], arr[i]]; // hoán đổi phần tử
  }
  return arr;
}


function getChoices(a, b){
  let choices = [];
  
  choices.push(a+b);

  for(var i=a+1;i >0 &&  choices.length < 4;i-- ){
    for(var j=b+1;j >0 &&  choices.length < 4;j-- ){
      if (!choices.find((e) => e === i+j)){
        choices.push(i+j);
      }
    }
  }

  choices = shuffleArray( choices);
  
  return choices;
}

